* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  font-family: var(--nonheading);
}

:root {
  --blue: #0d1c6b;
  --white: #ffffff;
  --black: #000000;
  --light_black: #4d4d4d;
  --grey: #b3b3b3;
  --off_white: #f0eded;
  --fade_sky: #b6bfd3;
  --sky: #b3caff;
  --nonheading: 'Poppins', sans-serif;
  --heading: 'Playfair Display', serif;
}
.product-container {
    overflow: hidden;
}

/* top-section */

.product-category {
    box-shadow: 0px 2px 20px 0px rgb(110, 110, 110);
    font-weight: bolder;
    font-family: 'Open Sans', sans-serif;
    position: -webkit-sticky;
    position: sticky;
}

.product-opacity {
    opacity: 0.3;
    z-index: -1;
}

.addtocart-status {
    position: fixed;
    left: calc(50% - 11%);
    top: calc(50% - 22px);
    height: 45px;
    width: 23%;
    background-color: darkblue;
    color: white;
    display: flex;
    z-index: 22;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
}

.products {
    display: flex;
    padding: 7px 40px;
    justify-content: space-between;
    font-size: calc(6px + 1.2vw);
}

.category-heading {
    font-weight: 700;
    font-size: calc(11px + 1.1vw);
}

.category-options {
    display: flex;
    width: 70%;
    justify-content: space-between;
    list-style: none;
    padding: 5px 0 10px 40px;
    font-size: calc(6px + 1vw);
}

.cart-icons {
    transform: scale(1.4);
}

.product-section {
    width: 95%;
    margin: 0 auto;
}

.recommended-heading {
    margin: 29px 0 29px 28px;
    color: darkblue;
    font-size: calc(8px + 1.7vw);
}

.other-heading {
    margin: 29px 0 29px 28px;
    color: darkblue;
    font-size: calc(8px + 1.7vw);
}

.cards-item {
    display: flex;
    flex-wrap: wrap;
}

.basket-length {
    position: absolute;
    top: 0px;
    color: red;
}

@media screen and (min-width:1000px) {
    .basket-length {
        top: -5px;
    }
}

@media screen and (max-width:900px) {
    .addtocart-status {
        left: calc(50% - 12%);
        top: calc(50% - 20px);
        height: 40px;
        width: 25%;
    }
}

@media screen and (max-width:700px) {
    .addtocart-status {
        left: calc(50% - 15%);
        top: calc(50% - 16px);
        height: 33px;
        width: 30%;
    }
}

@media screen and (max-width:450px) {
    .addtocart-status {
        left: calc(50% - 20%);
        top: 50%;
        height: 55px;
        width: 42%;
    }
    .category-options {
        padding-left: 23px;
        width: 80%;
    }
    .products {
        padding-left: 23px;
        padding-right: 14px;
    }
    .cart-icons {
        transform: scaleY(1);
    }
}
.cards{
    width: 25%;
    display: flex;
    justify-content: center;
}

.cards-list {
    display: flex;
    width: 80%;
    margin: 1rem 0;
    border: 1px solid gray;
    border-radius: 10px 10px 0 0;
}


.cards-list-link {
    display: flex;
    flex-flow: column;
    width: 100%;
    border-radius: 10px;
    /* overflow: hidden; */
    text-decoration: none;
    position: relative;
}

.cards-list-link::after{
    content: attr(data_category);
    position: absolute;
    top: -11px;
    left: 22px;
    border: 2px solid lightskyblue;
    color: lightskyblue;
    width: 42%;
    background-color: #fff;
    text-align: center;
    border-radius: 15px;
    font-size: 12px; 
}


.cards-list-picwrap {
    position: relative;
    width: 100%;
    padding-top: 85%;
    /* overflow: hidden; */
}

.cards-list-picwrap::after{
    content: attr(age);
    position: absolute;
    color: darkblue;
    right: -25px;
    top: 57px;
    width: calc(20% + 1vw);
    background-color: #fff;
    text-align: center;
    font-size: 10px;
    font-weight: 100;
    box-shadow: 0px 0px 10px 0px #bebdbdc9 inset;
    border-radius: 15px;
    padding: 5px 0;
}

.cards-list-picwrap::before{
    content: attr(data);
    position: absolute;
    padding: 1px 0;
    right: -25px;
    width: 30%;
    top: 23px;
    color: darkblue;
    background-color: #fff;
    text-align: center;
    border: 2px solid darkblue;
    border-radius: 15px;
    z-index: 1;
    font-size: 12px;
}

.card-list-img {
    position: absolute;
    top: 4%;
    right: 0;
    bottom: 0;
    left: 10%;
    display: block;
    width: 70%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    padding: 10px;
    padding-bottom: 21px;
}

.like-icon{
    position: relative;
    float: right;
    right: 15px;
    bottom: 8px;
}

.onclick_like_icon{
    color: red;
}


.card-list-info{
    position: relative;
    padding: 10px 9px 11px;
    border-top: 1px solid gray ;
}

.text-rating{
    display: flex;
    justify-content: space-between;
    font-size: calc(1px + 1vw);
}

.product-rating{
    display: flex;
}
.product-star-icon{
    color: goldenrod;
    padding-bottom: 3px;
}

.product-card-text{
    font-weight: bolder;

}

.product-rating-point{
    font-size: calc(4px + 1vw);
}

.card-list-info > p{
    font-size: calc(3px + 0.5vw);
    font-weight: 600;
    height: 60px;
    width: 100%;
    margin: 4px 0;
}


.product-price{
    font-size: 16px;
    margin: 10px 0px;
}


.new_price{
    margin-right: 10px;
}

.old_price{
    color: lightgray;
    text-decoration: line-through;
}
.product_btn{
    width: calc(60% + 1vw);
    background-color: darkblue;
    color: white;
    padding: 9px 5px;
    border: none;
    border-radius: 20px;
    margin: 10px 2px 0 20px;
}

.card_cart_icons{
    cursor: pointer;
    transform: scale(2);
    margin-top: 5px;
    margin-left: 10px;
    padding: 5px 0 0 0;
}

.card_cart_icons:active{
    color: red;
    padding: 0;
}
/* responsive  */


@media screen and (max-width:970px) {
    .cards-list-picwrap::before{
        font-size: 10px;
        padding: 3px 0;
    }
    .product-rating-point {
        font-size: calc(6px + 1.3vw);
    }
    .product_btn {
        margin-left: calc(13px + 0.1vw);
    }
    .card_cart_icons {
        transform: scale(1.7);
        margin-left: 5px;
    }
}


@media screen and (max-width:800px) {
    .cards {
        width: 50%;
    }
    .product-card-text{
        font-size: 13px;
    }
    .card-list-info > p{
        font-size: 10px;
    } 

    .product-rating-point {
        font-size: calc(11px + 1vw);
    }

    .product_btn {
        margin-left: calc(20px + 0.5vw);
    }

    .card_cart_icons {
        transform: scale(2);
        margin-left: 5px;
        margin-right: calc(6px + 2vw);
    }
}


@media screen and (max-width:570px){
    .card_cart_icons {
        margin-right: calc(4px + 1vw);
    }
}

@media screen and (max-width:500px) {
    .cards {
        width: 100%;
    }

    .cards-list{
        width: 70%;
    }

    .recommended-heading{
        margin: 29px 0 29px 40px;
        font-size: calc(12px + 2vw);
    }

    .other-heading {
        margin: 29px 0 29px 40px;
        font-size: calc(12px + 2vw);
    }
    
    .cards-list-link::after{
        width:30%;
        left: 30px;
        padding: 2px 0;
    }

    .cards-list-picwrap::before{
        width: 25%;
        top: 40px;
    }

    .cards-list-picwrap::after{
        top: 80px;
        width: calc(21% + 1vw);
    }
    .product-card-text{
        font-size: 13px;
    }
    .card-list-info > p{
        font-size: 10px;
    } 
    .product-rating-point {
        font-size: calc(13px + 1vw);
    }
    .product_btn {
        margin-left: calc(20px + 4vw);
    }
}


@media screen and (max-width:450px) {
    .cards-list-link::after{
        width:35%;
        left: 30px;
        padding: 2px 0;
    }

    .cards-list-picwrap::before{
        width: 25%;
        top: 30px;
    }

    .cards-list-picwrap::after{
        top: 70px;
        width: calc(21% + 1vw);
    }

}


@media screen and (min-width:1500px) {
    .text-rating{
        height: auto;
    }
    .card-list-info > p{
        font-size: calc(4px + 0.5vw);
    }

    .product-price {
        font-size: 20px;
    }
    .product_btn{
        font-size: 18px;
        padding: 6px 5px;
    }
    .product-star-icon{
        padding-top: 6px;
        transform: scale(1.9);
    }
    .product-rating-point{
        margin-left: 4px;
    }
}


@media screen and (min-width: 2000px){
    .cards-list-link::after{
        font-size: 19px;
        padding: 2px;
    }
    .cards-list-picwrap::before{
        font-size: 18px;
        padding: 5px 0;
        right: -35px;
    }
    .cards-list-picwrap::after{
        padding: 7px 0;
        font-size: 14px;
        top: 88px;
        right: -33px;
    }
    .product-star-icon{
        padding-top: 10px;
        transform: scale(2.5);
    }
    .card-list-info > p {
        font-size: calc(6px + 0.5vw);
    }
    .product-price {
        font-size: 25px;
    }
    .product_btn{
        font-size: 22px;
        padding: 6px 5px;
        margin: 10px 2px 0 60px;
    }
    .card_cart_icons{
        transform: scale(2.2);
        margin-left: 20px;
    }
}

@media screen and (min-width: 2500px){
    .cards-list-link::after{
        font-size: 25px;
        padding: 5px;
        top: -12px;
    }
    .cards-list-picwrap::before{
        font-size: 25px;
        padding: 5px 0;
        right: -50px;
        top: 50px;
    }
    .cards-list-picwrap::after{
        padding: 7px 0;
        font-size: 20px;
        top: 120px;
        right: -45px;
    }
    .product-star-icon{
        padding-top: 10px;
        transform: scale(2.5);
    }
    .card-list-info > p {
        font-size: calc(8px + 0.5vw);
        margin: 10px 0;
    }
    .product-price {
        font-size: 30px;
        margin: 15px 1px;
    }
    .product_btn{
        font-size: 27px;
        padding: 8px 5px;
        margin: 10px 2px 0 70px;
    }
    .card_cart_icons{
        transform: scale(2.8);
        margin-left: 20px;
        padding: 2PX 0 4PX 0;
    }
}
