.loader, .loader:before, .loader:after {
   border-radius: 50%;
   width: 2.5em;
   height: 2.5em;
   animation-fill-mode: both;
   animation: load7 1.8s infinite ease-in-out;
}

.loader {
   color: #0D286C;
   /* color: #ffffff; */
   font-size: 10px;
   margin: 80px auto;
   position: relative;
   text-indent: -9999em;
   transform: translateZ(0);
   animation-delay: -0.16s;
}

.loader:before, .loader:after {
   content: '';
   position: absolute;
   top: 0;
}

.loader:before {
   left: -3.5em;
   animation-delay: -0.32s;
}

.loader:after {
   left: 3.5em;
}

@keyframes load7 {
   0%, 80%, 100% {
      box-shadow: 0 2.5em 0 -1.3em;
   }
   40% {
      box-shadow: 0 2.5em 0 0;
   }
}
* {
  font-weight: 700;
  /* text-transform: capitalize; */
}

.product_container {
  text-transform: capitalize;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: calc(15px + 2vw) calc(10px + 2vw);
  background-color: var(--white);
}

.product_image img {
  width: 90%;
  height: 90%;
  margin-top: 10px;
  padding: 5px 5px;
}

.sliding_icon_left, .sliding_icon_right {
  display: none;
}

.product_details {
  display: flex;
  flex-direction: column;
}

.item_category {
  display: inline;
  font-size: calc(5px + 1vw);
}

.product_category_details_left {
  padding: calc(1px + 0.7vw) calc(3px + 2vw);
  border: 1px solid var(--blue);
  background: var(--blue);
  border-bottom-left-radius: 30px;
  border-top-left-radius: 30px;
  color: var(--white);
}

.product_category_details_right {
  padding: calc(1px + 0.7vw) calc(3px + 2vw);
  border: 1px solid var(--grey);
  background: var(--white);
  border-bottom-right-radius: 30px;
  border-top-right-radius: 30px;
  color: var(--blue);
}

.product_details_heading {
  margin-top: calc(20px + 1vw);
}

.rating {
  font-size: calc(6px + 1vw);
  margin-left: calc(5px + 1vw);
}

.product_title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product_name {
  color: var(--blue);
  font-size: calc(8px + 2vw);
}

.product_info {
  color: var(--light_black);
  font-size: calc(1px + 1vw);
}

.product_details_options {
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(20px + 1vw);
}

.about_product {
  border: 1px solid var(--light_black);
  border-radius: 30px;
  padding: calc(1px + 0.5vw) calc(3px + 2vw);
  margin: calc(2px + 0.2vw) calc(0px + 0.5vw);
  cursor: pointer;
  text-transform: uppercase;
  font-size: calc(1px + 1.1vw);
}

.about_product:hover {
  background: var(--grey);
}

.product_price {
  margin-top: calc(20px + 1vw);
  font-size: calc(5px + 1vw);
}

.product_current_price {
  font-size: calc(4px + 1.3vw);
}

.product_mrp {
  color: var(--blue);
  font-size: calc(4px + 1.3vw);
