.input_block:-ms-input-placeholder {
  color: #8a8686;
  font-weight: 500;
}
.input_block::placeholder {
  color: #8a8686;
  font-weight: 500;
}
.input_container {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 10px 4px;
}
.default_checkbox {
  margin: 0px 5px;
}
.default_address {
  color: #8a8686;
}
.form_buttons {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 12px;
}
.save_button {
  padding: 7px 15px;
  color: white;
  font-size: 1.2rem;
  background-color: #0d286c;
  border: 1px solid #0d286c;
  border-radius: 5px;
}
.cancel_button {
  font-size: 1.2rem;
  padding: 7px 15px;
  border: none;
  background-color: transparent;
  font-weight: bold;
  color: #0d286c;
}
.select_option {
  background-color: #c4c4c4;
}
button:hover {
  cursor: pointer;
}
.errormessage {
  color: red;
}
.error {
  border: 1px solid red;
}
/* Price container style */
.Price_container {
  min-width: 20%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 5px;
  margin-top: 10px;
}
.priceDetail {
  text-align: center;
  background-color: #0d286c;
  margin: 7px;
  color: white;
  padding: 8px 0px;
  border-radius: 5px;
}
.name_and_price {
  display: flex;
  justify-content: space-between;
  margin: 6px 0px;
  padding: 0px 9px;
}
.total_order {
  font-weight: bold;
  background-color: #c9d4ee;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 3px;
  padding: 4px 5px;
}
.total_savings {
  font-weight: bold;
}
.offers {
  color: #e99810;
  font-size: 11px;
  margin: 5px 0px;
}

/* media query part */

@media (max-width: 1100px) {
  .shipping_page {
    margin: 40px 10px;
