.order-summary-card {
  width: 100%;
  display: flex;
  flex-direction: column;
  border-radius: 4px;
}

.order-summary-card-header {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.order-summary-card-header span {
  margin-right: 10px;
}

.order-summary-card-orders-list {
  display: flex;
  flex-direction: column;
}

.order-item {
  display: flex;
  padding: 10px;
  justify-content: space-between;
}

.order-item-image {
  width: 30%;
  /* width: 220px;
   height: 120px; */
}

.order-item-info {
  display: flex;
  flex-direction: column;
}

.order-item-info h2 {
  font-size: 20px;
}

.orer-item-sect {
  display: flex;
  justify-content: space-between;
}

.quantity {
  padding: 2px 5px;
  display: flex;
  justify-content: space-between;
}

.quantity div {
  margin-left: 10px;
  margin-right: 10px;
  padding: 1px 5px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  cursor: pointer;
  border-radius: 50%;
  border: 1px solid black;
}

.quantity-value {
  font-size: 17px;
}

.remove-button {
  color: orange;
  cursor: pointer;
}

.remove-button:hover,
.quantity div:hover {
  transform: scale(1.02);
}

.delivery-date {
  font-size: 14px;
}

.order-item-info {
  padding: 1px 3px;
  display: flex;
  flex-direction: column;
}

