}

.Navbar_logo a {
  display: flex;
  justify-content: center;
  align-items: center;
}

.codesbots-logo {
  font-size: 30px;
  color: white;
  justify-content: center;
  align-items: center;
}

.Navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* padding: 11px 35px; */
  padding: 20px 35px;
  color: white;
  font-size: 1.3rem;
  background-color: #0d286c;
}

.Navbar_options {
  width: 50%;
  display: flex;
  justify-content: space-around;
}

.Navbar_options>a {
  text-decoration: none;
