.heading {
  margin-top: 50px;
  position: relative;
  /* left: 92px; */
  left: 50px;
  height: 73px;
  width: -webkit-max-content;
  width: max-content;
  display: flex;
  color: #5a2969;
  justify-content: flex-start;
  align-items: flex-end;
  margin-bottom: 20px;
  background-image: url(/static/media/heading-background.d012e7a4.svg);
}

.heading h3 {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  padding: 13px;
  line-height: 24px;
  padding-top: 25px;
  letter-spacing: 0.025em;
}

@media screen and (min-width: 1600px) {
  .heading h3 {
    font-size: 2rem;
  }

  .heading {
    background-image: 100% 100%;
  }
}

