  .heading {
    left: 30px;
    /* width: fit-content; */
    height: 50px;
    overflow-y: hidden;
    margin-bottom: 10px;
  }

  .heading h3 {
    font-weight: bold;
    font-size: 18px;
    padding: 1px 4px;
    line-height: 24px;
    letter-spacing: 0.3px;
  }
}

