  /* transition: all 2s; */
  animation: popup 0.2s ease-in-out;
}
@keyframes popup {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0%);
  }
}
.modal_popdown {
  /* transition: all 2s; */
  animation: popdown 0.2s ease-in-out;
}
@keyframes popdown {
  0% {
    transform: translateY(0%);
  }
  100% {
    transform: translateY(-100%);
  }
}
