@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&family=Poppins:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&display=swap');

.onlymobile {
   display: none;
}

.program-sec {
   display: flex;
   color: white;
   background-color: #3E2F69;
   flex-direction: column;
   padding: 10px;
   margin-top: 30px;
   margin-left: 50px;
   margin-right: 50px;
   border-radius: 41px;
   margin-bottom: 50px;
   padding-bottom: 30px;
}

.ps-upper {
   overflow-y: hidden;
   padding-top: 20px;
   display: flex;
}

.ps-upper-text {
   display: flex;
   flex-direction: column;
   width: 25%;
   align-items: flex-start;
   padding: 30px;
}

.ps-upper-text h2 {
   font-family: var(--heading);
   font-style: normal;
   font-weight: bold;
   font-size: 45px;
}

.ps-upper-text p {
   font-family: var(--nonheading);
   margin-top: 10px;
   font-size: 18px;
   font-weight: 400;
   letter-spacing: 2px;
}

.ps-upper-image {
   width: 75%;
   height: 350px;
   /* background-image: url('../../asserts/program-girl.svg'); */
}

.ps-inside-image {
   width: 75%;
   height: 350px;
   /* background-image: url('../../asserts/program-girlinside.svg'); */
}

.ps-lower {
   overflow-y: hidden;
   display: flex;
   justify-content: center;
   align-items: center;
}

.lower-card {
   justify-content: center;
   align-items: center;
   padding: 10px;
   display: flex;
   flex-direction: column;
   margin-bottom: 20px;
}

.lc-img {
   z-index: 9999;
   width: 100px;
   height: 100px;
   margin-bottom: 20px;
}

.lc-text {
   display: flex;
   flex-direction: column;
}

.lc-text h3 {
   font-family: 'Playfair Display', serif;
   margin-bottom: 10px;
   font-size: 30px;
   font-weight: 400;
   font-family: Poppins;
   font-style: normal;
   font-weight: 500;
}

.lc-text p {
   font-family: 'Poppins', sans-serif;
   padding-bottom: 10px;
   letter-spacing: 2px;
   font-weight: 400;
   padding-right: 5px;
}

.learnbtn {
   color: black;
   width: 120px;
   font-size: 16px;
   display: flex;
   justify-content: center;
   align-items: center;
   background: #D9D9D9;
   padding: 7px 10px;
   border-radius: 5px;
   margin-top: 10px;
   cursor: pointer;
   font-weight: 600;
}

.content {
   display: flex;
}

.line-lower-card {
   margin-top: 20px;
   z-index: 999;
   width: 50px;
   height: 170px;
   background-image: url('../../asserts/line.svg');
}

@media (max-width:760px) {
   .onlymobile {
      display: block;
   }

   .program-sec {
      padding: 5px;
      margin: 20px;
      margin-top: 10px;
      padding-bottom: 20px;
   }

   .ps-upper {
      flex-direction: column;
      padding-top: 10px;
      justify-content: center;
      align-items: center;
   }

   .content {
      flex-direction: column;
      justify-content: center;
      align-items: center;
   }

   .ps-upper-text {
      width: 75%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 10px;
   }

   .ps-upper-text h2 {
      font-size: 25px;
      letter-spacing: 0.5px;
   }

   .ps-upper-text p {
      font-size: 15px;
      font-weight: 400;
      line-height: normal;
      letter-spacing: 0.6px;
   }

   .ps-upper-image {
      margin-top: 5px;
      width: 90%;
      /* background-image: url('../../asserts/program-mobile-img.png'); */
      height: 200px;
   }

   .ps-lower {
      overflow-y: hidden;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
   }

   .lower-card {
      margin: 0px;
      padding: 0px;
      margin-bottom: 0px;
   }

   .lc-img {
      z-index: 9999;
      /* width: 80px; */
      width: 100px;
      /* height: 80px; */
      height: 100px;
   }

   .lc-text {
      justify-content: center;
      align-items: center;
      padding: 5px;
   }

   .lc-text h3 {
      font-size: 20px;
      overflow-y: hidden;
      line-height: inherit;
      letter-spacing: 0.025em;
      margin-bottom: 2px;
   }

   .lc-text p {
      text-align: center;
      font-size: 15px;
      margin-top: 10px;
      padding-right: 20px;
      padding-left: 20px;
      letter-spacing: 0.5px;
      line-height: inherit;
      padding-bottom: 5px;
   }

   .learnbtn {
      width: fit-content;
      padding: 2px 5px;
      margin-top: 4px;
      font-size: 16px;
   }

   .line-lower-card {
      margin-top: 0px;
      width: 120px;
      height: 140px;
      transform: rotate(90deg);
      background-image: url('../../asserts/line.svg');
   }
}

@media (min-width:760px) and (max-width:1020px) {
   .program-sec {
      padding: 5px;
      margin-left: 40px;
      margin-right: 40px;
      padding-bottom: 30px;
   }

   .ps-upper {
      padding-top: 10px;
   }

   .ps-upper-text {
      width: 40%;
      padding: 10px;
   }

   .ps-upper-text h2 {
      font-size: 25px;
      letter-spacing: 0.5px;
   }

   .ps-upper-text p {
      margin-top: 10px;
      font-size: 15px;
      letter-spacing: 0.6px;
      line-height: normal;
   }

   .ps-upper-image {
      width: 60%;
      height: 190px;
   }

   .lower-card {
      margin: 10px;
      padding: 0px;
      margin-bottom: 0px;
   }

   .lc-img {
      width: 80px;
      height: 80px;
      margin-bottom: 20px;
   }

   .lc-text {
      padding: 5px;
   }

   .lc-text h3 {
      font-size: 18px;
      overflow-y: hidden;
      line-height: 20px;
      letter-spacing: 0.3px;
      margin-bottom: 2px;
   }

   .lc-text p {
      font-size: 15px;
      margin-top: 10px;
      letter-spacing: 0.5px;
      line-height: 20px;
      padding-bottom: 5px;
      padding-right: 2px;
   }

   .learnbtn {
      width: fit-content;
      padding: 4px 4px;
      margin-top: 4px;
      text-align: center;
      letter-spacing: 0.4px;
      font-size: 10px;
      font-weight: 600;
   }

   .line-lower-card {
      margin-top: 0px;
      width: 30px;
      height: 80px;
      background-image: url('../../asserts/line.svg');
   }
}

@media screen and (min-width:2500px) {
   .ps-upper-text h2 {
      font-size: 5.5rem;
   }

   .ps-upper-text p {
      width: 100%;
      font-size: 2rem;
   }

   .lc-img {
      width: 170px;
      height: 170px;
   }

   .lc-text h3 {
      font-size: 3rem;
   }

   .lc-text p {
      letter-spacing: 1px;
      font-size: 2rem;
   }

   .learnbtn {
      width: fit-content;
      letter-spacing: 1px;
      font-size: 2rem;
   }

   .ps-inside-image {
      width: 80%;
      height: 400px;
   }
}

@media screen and (max-width:2500px) and (min-width:1800px) {
