   }

   .lc-text h3 {
      font-size: 3rem;
   }

   .lc-text p {
      letter-spacing: 1px;
      font-size: 1.5rem;
   }

   .learnbtn {
      width: fit-content;
      letter-spacing: 1px;
      font-size: 1.5rem;
   }
}

